<script>
import { defineComponent } from 'vue'
import { ZoomIn,ZoomOut,Pencil,Undo,RectangleOne,MoveOne,HighLight,Left,Right,Drag,Download,TextStyle,PictureOne} from '@icon-park/vue-next';

export default defineComponent ({
  data(){
    return{
      type: "move",
      draged: false,
      disx: 0,
      disy: 0,
      textColor: "#000",
      draggedImage: null,
    }
  },
  components: {
    ZoomIn,
    ZoomOut,
    Pencil,
    Undo,
    RectangleOne,
    MoveOne,
    HighLight,
    Left,
    Right,
    Drag,
    Download,
    TextStyle,
    PictureOne
  },

  // 自定义指令 实现可拖动  在标签上使用v-drag就可以了
  directives: {
    drag(el, bindings) {
      el.onmousedown = function(e) {
        var disx = e.pageX - el.offsetLeft
        var disy = e.pageY - el.offsetTop
        document.onmousemove = function (e) {
          console.log(e.pageX,e.pageY)
          el.style.left = e.pageX - disx + 'px'
          el.style.top = e.pageY - disy + 'px'
        }
        document.onmouseup = function() {
          document.onmousemove = document.onmouseup = null
        }
      }
    }
  },

  methods: {
    switchtools(e) {
      this.type = e.currentTarget.id;
      this.$parent.data_style = this.type;
    },
    dragdown(e) {
      this.draged = true
      const el = this.$refs.toolsbox
      document.addEventListener('mousemove',this.dragmove)
      this.disx = e.pageX - el.offsetLeft
      this.disy = e.pageY - el.offsetTop
    },
    dragup() {
      this.draged = false
      const el = this.$refs.toolsbox
      document.removeEventListener('mousemove',this.dragmove)
    },
    dragmove(e) {
      var event = e;
      // var scrolltop = document.documentElement.scrollTop||document.body.scrollTop;
      if(this.draged == true){
        const el = this.$refs.toolsbox
        // console.log(el.offsetLeft, el.offsetTop)
        // console.log(disx,disy)
        el.style.left = e.pageX - this.disx + 'px';
        el.style.top = e.pageY - this.disy + 'px';
      }      
    },

    getTextColor(e,color) {
      let colorItems = document.querySelectorAll('.color-item');
      let item = e.currentTarget
      colorItems.forEach(i => i.classList.remove('active'));
      item.classList.add('active');
      this.textColor = color
      let texta = document.getElementById("edit-textarea");
      texta.style.color = this.textColor
    },
    getPenColor(e,color) {
      let colorItems = document.querySelectorAll('.color-item');
      let item = e.currentTarget
      colorItems.forEach(i => i.classList.remove('active'));
      item.classList.add('active');
      this.$parent.penColor = color
      
    },

    changePenWidth() {
      const range = document.getElementById('penWidth-range')
      console.log(range.value)
      this.$parent.penWidth = parseInt(range.value)
    },

    selectimg(e) {
      var image = e.currentTarget
      console.log(image.src)
      console.log(this.$parent.images)
      this.$parent.images.push(image.src)
      console.log(this.$parent.images)
    },

    
  }

})
</script>

