<template>
  <div class="header">
    <div class="headlist">
      <div class="head-item"  @click="openmenu" id="menuicon">
        <application-two theme="filled" size="22" fill="#363636"/>
      </div>
      <div class="head-item home" @click="goto('/')">
        <!-- <span class="logo">捌月</span> -->
        <home class="homeicon" theme="filled" size="22" fill="#363636" />
      </div>
    </div>
    <transition name="fade-slide">
      <div class="menu" v-if="menuisshow" ref="menu">
        <div class="menu-list">
          <div class="menu-list-title">工具</div>
          <div class="menu-item">app管理</div>
          <div class="menu-item" @click="goto('/tools/bulidanswersheet')">答题卡生成</div>
          <div class="menu-item">待定</div>
          <div class="menu-item">待定</div>
        </div>
      </div>
    </transition>  
  </div>
  <div class="header-block"></div>
  <div class="view">
    <router-view></router-view>
  </div>
</template>

<script>
import {ApplicationTwo, Home} from '@icon-park/vue-next'

export default {
  name: 'App',
  data(){
    return{
      menuisshow:false,
    }
  },
  components: {
    ApplicationTwo,
    Home,
  },
  mounted() {
    
  },
  unmounted() {
    document.removeEventListener('click',this.closemenu)
    console.log('销毁')
  },
  methods:{
    goto(path){
      this.$router.push(path)
      this.menuisshow = false
      document.removeEventListener('click',this.closemenu)
      console.log('销毁')
    },
    openmenu() {
      if(!this.menuisshow){
        this.menuisshow = true
        document.addEventListener('click',this.closemenu);
        console.log('添加成功')
      }else{
        this.menuisshow = false
        document.removeEventListener('click',this.closemenu)
        console.log('销毁')
      }
      
    },
    closemenu(e){
      const md = document.getElementById('menuicon')
      if(!md.contains(e.target) && !this.$refs.menu.contains(e.target)){
        if(this.menuisshow == true){
          this.menuisshow = false
          document.removeEventListener('click',this.closemenu)
          console.log('销毁')
        }
      }
      
      
    },
  }
}
</script>


<style>

@import url('@/assets/fonts/index.css');

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  background-color: #f7f9fe;
}

#app > * {
  background-color: #fff;
}

.view {
  margin: 20px 30px;
  padding: 10px 20px ;
  border: 1px solid #e3e8f7;
  border-radius:12px;

}


.header {
  height: 60px;
  border-bottom: 1px solid #e3e8f7;
  position: fixed;
  z-index: 9999;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  padding: 0 30px;
}

.head-list {
  width: 100%;
}

.head-item {
  display: inline-block;
  margin-right: 5px;
}

.header-block {
  height: 60px;
}

.fade-slide-enter-active, .fade-slide-leave-active {
  transition: opacity 0.5s, transform 0.5s;
}
.fade-slide-enter-from, .fade-slide-leave-to /* .fade-slide-leave-active in <2.1.8 */ {
  opacity: 0;
  transform: translateY(1em);
}
.fade-slide-enter-to, .fade-slide-leave-from /* .fade-slide-enter-active in <2.1.8 */ {
  opacity: 1;
  transform: translateY(0);
}

.menu{
  position: absolute;
  width: 342px;
  top:55px;
  border: 1px solid #e3e8f7;
  border-radius:12px;
  padding: 15px 0;
  background-color: #fff;
}

.menu:hover {
  border-color: #425aef;
}

.menu-list-title {
  font-size: 12px;
  margin: 0 10px;
  padding: 0 5px;
}

.menu-item {
  float: left;
  width: 141px;
  font-size: 16px;
  line-height: 30px;
  height: 30px;
  margin: 5px 10px;
  padding: 5px;
  border-radius:10px;
  cursor: pointer;
}

.menu-item:hover {
  background-color: #425aef;
  color: #fff;
}

